import { Divider } from '@material-ui/core'
import { useEffect, useState } from 'react'
import styled from 'styled-components'
import { getBUSDBalance, getDividends, getMMTBalance, getTotalEarn, getUnpaidEarnings } from '../../utils/contractHelper'
import Web3 from 'web3'
import BigNumber from 'bignumber.js'
import TokenIcon from '../../assets/img/logo.png'
import BusdIcon from '../../assets/img/busd.png'
import { useWeb3React } from '@web3-react/core'
import { InjectedConnector } from '@web3-react/injected-connector'


const StyledInput = styled.input`
    border: 0.6vh #caeea4 solid;
    outline-style: none;
    width: 56%;
    height: 6vh;
    font-size: 4vh;
    :focus{
        border:0.6vh #99CC66 solid;
    }
    :hover{
        border:0.6vh #99CC66 solid;
    }
    :active{
        border:0.6vh #99CC66 solid;
    }

`
const StyledButton = styled.div`
background-color: #99CC66;
padding:0 2vh;
border-radius: 1vh;
display: flex;
align-items: center;
justify-content: center;
cursor: pointer;
:hover{
    color:#FFFF66
}
`

const Dashboard = () => {
    const [searchWord, setSearchWord] = useState('')
    const [mmtBalance, setMmtBalance] = useState('--')
    const [busdBalance, setBusdBalance] = useState('--')
    const [unpaid, setUnpaid] = useState('--')
    const [rewardList, setRewardList] = useState([])
    const [baseline, setBaseline] = useState('--')
    const [totalDistributed, setTotalDistributed] = useState('--')
    const [totalPoolDistributed, setTotalPoolDistributed] = useState('--')
    const [realised, setRealised] = useState('--')
    const [myPoolReward, setMyPoolReward] = useState('--')
    const injected = new InjectedConnector({
        supportedChainIds: [42, 56]
    })
    const context = useWeb3React()
    const {
        connector,
        library,
        chainId,
        account,
        activate,
        deactivate,
        active,
        error
    } = context
    const checkAddress = async (keyword) => {
        const isAddress = Web3.utils.isAddress(keyword)
        if (!isAddress) { return }
        const res = await getMMTBalance(keyword)
        setMmtBalance(new BigNumber(Web3.utils.fromWei(res, 'gwei')).toFormat(0))
        const res2 = await getBUSDBalance(keyword)
        setBusdBalance(new BigNumber(Web3.utils.fromWei(res2, 'mwei')).toFormat(2))
        const res3 = await getUnpaidEarnings(keyword)
        setUnpaid(new BigNumber(Web3.utils.fromWei(res3, 'mwei')).toFormat(2))
        const res4 = await getTotalEarn(keyword)
        console.log(res4, 'res4')
        setRealised(new BigNumber(Web3.utils.fromWei(res4.totalRealised, 'mwei')).toFormat(2))
        setMyPoolReward(new BigNumber(Web3.utils.fromWei(res4.poolReward, 'mwei')).toFormat(2))
    }
    const checkMyWallet = () => {
        if (account) {
            setSearchWord(account)
            checkAddress(account)
        }else{
            try {
                activate(injected);
            } catch (error) {
                console.error(error);
            }
        }

    }
    const shortAddress = (address) => {
        let left = address.substring(0, 4)
        let right = address.substring(address.length - 4, address.length)
        return left + '..' + right
    }
    useEffect(async () => {
        const res = await getDividends()
        let list = res.list.map(p => {
            p.address = shortAddress(p.address)
            p.reward = new BigNumber(Web3.utils.fromWei(p.reward, 'mwei')).toFormat(2)
            return p
        })
        setRewardList(list)
        setBaseline(new BigNumber(Web3.utils.fromWei(res.baseline, 'mwei')).toFormat(2))
        setTotalDistributed(new BigNumber(Web3.utils.fromWei(res.totalDistributed, 'mwei')).toFormat(2))
        setTotalPoolDistributed(new BigNumber(Web3.utils.fromWei(res.totalPoolDistributed, 'mwei')).toFormat(2))
        console.log(res, 'res3')
    }, [])
    useEffect(() => {
        if (account) {
            setSearchWord(account)
        }
    }, [account])

    return (
        <div>
            <div style={{ backgroundColor: '#99cc6642', marginTop: '4vh', padding: '2vh 0', borderRadius: '1vh' }}>
                <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                    <StyledInput placeholder='Wallet Address' value={searchWord} onChange={(v) => { setSearchWord(v.target.value) }}></StyledInput>
                    <StyledButton onClick={() => { checkAddress(searchWord) }} style={{ marginLeft: '2vh', height: '6vh' }}>CHECK</StyledButton>
                </div>
                <StyledButton onClick={checkMyWallet} style={{ width: '36vh', margin: '1vh auto 0 auto', padding: '1vh 0' }}>CHECK MY WALLET</StyledButton>

            </div>
            <div style={{ backgroundColor: '#f3f3a5', borderRadius: '1vh', padding: '2vh', display: 'flex', flexWrap: 'wrap', justifyContent: 'center', margin: '4vh 0' }}>
                <div style={{ width: '80vh', fontSize: '4vh', wordWrap: 'break-word', wordBreak: 'break-word' }}>
                    <div>Your Wallet</div>
                    <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                        <div>{mmtBalance}</div>
                        <img style={{ width: '6vh', height: '6vh' }} src={TokenIcon}></img>
                    </div>
                    <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                        <div>{busdBalance}</div>
                        <img style={{ width: '4vh', height: '4vh' }} src={BusdIcon}></img>
                    </div>
                    <Divider style={{ margin: '2vh 0' }}></Divider>
                    <div>Reflected Earned</div>
                    <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                        <div>{realised}</div>
                        <img style={{ width: '4vh', height: '4vh' }} src={BusdIcon}></img>
                    </div>
                </div>
                <div style={{ width: '80vh', fontSize: '4vh', wordWrap: 'break-word', wordBreak: 'break-word' }}>
                    <div>Reward Not Claimed</div>
                    <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                        <div>{unpaid}</div>
                        <img style={{ width: '4vh', height: '4vh' }} src={BusdIcon}></img>
                    </div>
                    {/* <StyledButton style={{ width: '36vh', margin: '2vh auto' }}>CLAIM MANUALLY</StyledButton> */}
                    <div style={{ fontSize: '2vh' }}>Rewards are automatically sent every 60 minutes. It can, however, take longer depending on your holdings and trading volume. Rewards will be triggered once they are big enough to cover the gas fees. If you are a smaller holder it may take from a couple hours to a few days for rewards to appear in your wallet. </div>
                </div>
            </div>
            <div style={{ backgroundColor: '#ff666657', borderRadius: '1vh', padding: '2vh', display: 'flex', flexWrap: 'wrap', justifyContent: 'center', margin: '4vh 0' }}>
                <div style={{ width: '80vh', fontSize: '4vh', wordWrap: 'break-word', wordBreak: 'break-word' }}>
                    <div>Rewards Baseline</div>
                    <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                        <div>{baseline}</div>
                        <img style={{ width: '4vh', height: '4vh' }} src={BusdIcon}></img>
                    </div>
                    <Divider style={{ margin: '2vh 0' }}></Divider>
                    <div>Pool Earned</div>
                    <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                        <div>{myPoolReward}</div>
                        <img style={{ width: '4vh', height: '4vh' }} src={BusdIcon}></img>
                    </div>
                    <div style={{ fontSize: '2vh', marginTop: '1vh' }}>Each transaction will have 1% fee to the reward pool. When the pool reaches a certain baseline, the reward pool will be triggered as long as there is a purchase behavior. Thus to get all the bonuses from the reward pool at once, the reward baseline will keep increasing after each bonus.  (The buyer participation amount is at least 1,000,000,000)</div>
                </div>
                <div style={{ width: '80vh', fontSize: '4vh', wordWrap: 'break-word', wordBreak: 'break-word' }}>
                    <div>Last 3 Winners</div>
                    <div style={{ display: 'flex', justifyContent: 'space-between', fontSize: '3vh' }}>
                        <div>address</div>
                        <div>rewards</div>
                        <div>blocks</div>
                    </div>
                    {
                        rewardList.map((p, index) => (
                            <div key={index} style={{ display: 'flex', justifyContent: 'space-between', fontSize: '3vh', margin: '1vh 0' }}>
                                <div>{p.address}</div>
                                <div>{p.reward}</div>
                                <div>{p.block}</div>
                            </div>
                        ))
                    }

                </div>
            </div>
            <div style={{ backgroundColor: '#99cc6642', borderRadius: '1vh', padding: '2vh', display: 'flex', flexWrap: 'wrap', justifyContent: 'center', margin: '4vh 0' }}>
                <div style={{ width: '80vh', fontSize: '4vh', wordWrap: 'break-word', wordBreak: 'break-word' }}>
                    <div>Reward Distributed To Holders</div>
                    <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', margin: '2vh 0' }}>
                        <div>{totalDistributed}</div>
                        <img style={{ width: '4vh', height: '4vh' }} src={BusdIcon}></img>
                    </div>
                    <div >Reward Distributed To Buyers</div>
                    <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', margin: '2vh 0' }}>
                        <div>{totalPoolDistributed}</div>
                        <img style={{ width: '4vh', height: '4vh' }} src={BusdIcon}></img>
                    </div>
                </div>
                <div style={{ width: '80vh', fontSize: '4vh', wordWrap: 'break-word', wordBreak: 'break-word' }}>
                    <div>$MMT Contract</div>
                    <div style={{ margin: '2vh 0' }}>0xeba863CEBAAf25682180Ff98DbF861c41c17C469</div>
                    <div>$BUSD Contract</div>
                    <div style={{ margin: '2vh 0' }}>0xe9e7CEA3DedcA5984780Bafc599bD69ADd087D56</div>
                </div>
            </div>
        </div>
    )
}

export default Dashboard