
import AppBar from '@material-ui/core/AppBar'
import './index.css'
import Logo from '../../assets/img/logo.png'
import MenuIcon from '../../assets/img/menu.png'
import DashboardIcon from '../../assets/img/dashboard.png'
import HomeIcon from '../../assets/img/home.png'
import NFTIcon from '../../assets/img/nft.png'
import { SwipeableDrawer } from '@material-ui/core'
import { useEffect, useState } from 'react'
import styled from 'styled-components'
import { useHistory, useLocation } from 'react-router-dom'
import TG from '../../assets/img/telegram.svg'
import TW from '../../assets/img/twitter.svg'
import { useWeb3React } from '@web3-react/core'
import { InjectedConnector } from '@web3-react/injected-connector'

const NavButton = styled.div`
    background: ${props => props.active ? '#99cc6645' : ''} ;
    display:flex;
    align-items: center;
    text-align: left;
    color:#99CC66;
    margin:1rem;
    padding:0.4rem;
    width:8rem;
    font-size: 1rem;
    border-radius:0.4rem;
    cursor: pointer;
    &:hover{
        border:${props => props.active ? '' : '1px #99CC66 solid'}
    }
`
const StyledButton = styled.div`
background-color: #99CC66;
padding:1vh 2vh;
border-radius: 1vh;
display: flex;
align-items: center;
justify-content: center;
cursor: pointer;
:hover{
    color:#FFFF66
}
`
const SwtichSpecificNetwork = async () => {
    if (typeof window.ethereum !== 'undefined') {
        console.log('MetaMask is installed!')
        const ethereum = window.ethereum
        try {
            await ethereum.request({
                method: 'wallet_switchEthereumChain',
                params: [{ chainId: '0x38' }],
            });
        } catch (switchError) {
            console.log(switchError)
            // This error code indicates that the chain has not been added to MetaMask.
            try {
                await ethereum.request({
                    method: 'wallet_addEthereumChain',
                    params: [{ chainId: '0x38', rpcUrls: ['https://bsc-dataseed.binance.org/'], chainName: 'Binance Smart Chain' }],
                });
            } catch (addError) {
                console.log(addError)
                // handle "add" error
            }
            // handle other "switch" errors
        }
    } else {
        console.log('need installed MetaMask')
    }
}

const Header = () => {
    const [visible, setVisible] = useState(false)
    const [currentTab, setCurrentTab] = useState('home')
    const [action, setAction] = useState('robot-off')
    const history = useHistory()
    const location = useLocation()
    const [address, setAddress] = useState('--')
    const injected = new InjectedConnector({
        supportedChainIds: [42, 56]
    })
    const context = useWeb3React()
    const {
        connector,
        library,
        chainId,
        account,
        activate,
        deactivate,
        active,
        error
    } = context
    const showMenu = (action, show) => {
        setAction(action)
        setVisible(show)
    }
    const NavTo = (url) => {
        setCurrentTab(url)
        showMenu('robot-off', false)
        history.push(url)
    }
    const handleConnect = () => {
        try {
            activate(injected);
        } catch (error) {
            console.error(error);
        }
    }
    useEffect(() => {
        if (location.pathname.indexOf('home') >= 0) { setCurrentTab('home') }
        else if (location.pathname.indexOf('dashboard') >= 0) { setCurrentTab('dashboard') }
    }, [location])
    useEffect(() => {
        if (account) {
            setAddress(`${account.substring(0, 4)}..${account.substring(account.length - 4, account.length)}`)
        }
    }, [account])
    useEffect(() => {
        SwtichSpecificNetwork()
        handleConnect()
    }, [])


    return (
        <AppBar position='sticky'>
            <div className='color3' style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                <img onClick={() => { showMenu('robot-on', true) }} style={{ width: '4vh', height: '4vh', cursor: 'pointer' }} src={MenuIcon}></img>
                <div style={{ display: 'flex', justifyContent: 'flex-start', alignItems: 'center' }}>
                    <img onClick={() => { showMenu('robot-jump', false) }} className={action} style={{ width: '8vh', height: '8vh', cursor: 'pointer' }} src={Logo}></img>
                    <div style={{ fontSize: '4vh', fontWeight: 700 }}>Money Machine</div>
                </div>
                <div style={{ opacity: 0.001, width: '0.01vh' }}>App</div>
            </div>
            <SwipeableDrawer
                anchor='left'
                open={visible}
                onClose={() => { showMenu('robot-off', false) }}
                onOpen={() => { }}
                style={{ position: 'relative' }}
            >
                <div >
                    <NavButton onClick={() => { NavTo('home') }} active={currentTab === 'home'}>
                        <img style={{ width: '1.4rem', height: '1.4rem' }} src={HomeIcon}></img>
                        <div style={{ marginLeft: '1.6rem' }}>Home</div>
                    </NavButton>
                    <NavButton onClick={() => { NavTo('dashboard') }} active={currentTab === 'dashboard'}>
                        <img style={{ width: '1.4rem', height: '1.4rem' }} src={DashboardIcon}></img>
                        <div style={{ marginLeft: '1.6rem' }}>Dashboard</div>
                    </NavButton>
                    <NavButton onClick={() => { NavTo('nft') }} active={currentTab === 'nft'}>
                        <img style={{ width: '1.4rem', height: '1.4rem' }} src={NFTIcon}></img>
                        <div style={{ marginLeft: '1.6rem' }}>NFT</div>
                    </NavButton>
                    {
                        account ?
                            <div style={{ backgroundColor: 'rgba(0,0,0,0.4)', margin: '2vh', textAlign: 'center', color: '#FFFF66', borderRadius: '1vh', padding: '1vh 0' }}>
                                {address}
                            </div>
                            :
                            <StyledButton onClick={handleConnect} style={{ margin: '2vh' }}>Connect Wallet</StyledButton>

                    }

                    <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-around', position: 'absolute', bottom: '4vh', left: '2vh' }}>
                        <a href='https://mobile.twitter.com/money_machine__' target='_blank'>
                            <img style={{ width: '5vh', height: '5vh', cursor: 'pointer' }} src={TW}></img>
                        </a>
                        <a href='https://t.me/MoneyMachine_Official' target='_blank'>
                            <img style={{ width: '5vh', height: '5vh', marginLeft: '2vh', cursor: 'pointer' }} src={TG}></img>
                        </a>
                    </div>
                </div>
            </SwipeableDrawer>
        </AppBar>
    )
}

export default Header