
import './index.css'
import ReactECharts from 'echarts-for-react'
import ReusdIcon from '../../assets/img/reusd.png'
import RewardsIcon from '../../assets/img/rewards.png'
import LiquidityIcon from '../../assets/img/liquidity.png'
import BuybackIcon from '../../assets/img/buyback.png'
import SharkIcon from '../../assets/img/shark.png'
import CheckedIcon from '../../assets/img/checked.png'
import UncheckIcon from '../../assets/img/uncheck.png'
import TG from '../../assets/img/telegram.svg'
import TW from '../../assets/img/twitter.svg'
import styled from 'styled-components'
import { useHistory } from 'react-router-dom'


const StyledButton = styled.div`
background-color: #99CC66;
padding:1vh 2vh;
border-radius: 1vh;
cursor: pointer;
:hover{
    color:#FFFF66
}
`

const Index = () => {
    const history = useHistory()
    const NaviTo = () => {
        history.push('dashboard')
    }
    const option = {

        tooltip: {
            trigger: 'item'
        },
        legend: {
            orient: 'horizontal',
            bottom: 'bottom',
        },
        series: [
            {
                name: '$MMT',
                type: 'pie',
                radius: '50%',
                data: [
                    { value: 45, name: 'Pre-Sale and Initial Liquidity' },
                    { value: 50, name: 'Sent to Burn Address' },
                    { value: 4, name: 'Founders and Team' },
                    { value: 1, name: 'Airdrop' }
                ],
                emphasis: {
                    itemStyle: {
                        shadowBlur: 10,
                        shadowOffsetX: 0,
                        shadowColor: 'rgba(0, 0, 0, 0.5)'
                    }
                }
            }
        ]
    };
    return (
        <div>


            <div className='color2' style={{ fontSize: '4vh', margin: '4vh 0 2vh 0', fontWeight: 700 }}>HYPER DEFLATIONARY TOKEN WITH REFLECTIONS IN BUSD</div>
            <div style={{ color: '#918e8e', marginBottom: '4vh' }}>make money is so simple</div>
            <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', marginBottom: '4vh' }}>
                <StyledButton style={{ marginRight: '3vh' }}>BUY NOW</StyledButton>
                <StyledButton onClick={NaviTo}>CHECK REWARDS</StyledButton>
            </div>

            <div style={{ backgroundColor: '#99cc6642', borderRadius: '1vh', padding: '2vh' }}>
                <div className='color2' style={{ marginBottom: '1vh' }}>WHY HOLD Money Machine Token?</div>
                <div style={{ textAlign: 'left' }}>
                    <div >$MMT is a deflationary token designed to become more scarce over time. All holders of Money Machine will earn an 8% reward from every Buy/Sell Transaction in Binance pegged BUSD, which is automatically sent to your wallets.</div>
                    <div style={{ margin: '2vh 0' }}>A chance to earn a big rewards from rewards pool every time you buy, which is immediately sent to your wallets.</div>
                    <div >Funded by strategic buyback, 1% tokens are collected from every transaction and converted to BNB, and are stored in Our contract. When BuyBack Wallet is enabled It purchases Money Machine directly from exchanges and instantly removes the purchased tokens permanently from circulating supply while making green candles on the price chart. 2% of every transaction is transferred into the liquidity pool for Pancakeswap to create a stable price floor.</div>

                </div>
            </div>
            <div className='color2' style={{ marginTop: '6vh' }}>TOKENECONOMICS</div>

            <div style={{ display: 'flex', flexWrap: 'wrap', justifyContent: 'center', margin: '4vh 0' }}>
                <div style={{ width: '40vh', margin: '2vh' }}>
                    <div style={{ borderRadius: '2vh 2vh 0 0', backgroundColor: '#99CC66', padding: '2vh 0' }}>Reward to Holders</div>
                    <div className='color2' style={{ borderRadius: '0 0 2vh 2vh', backgroundColor: '#FFFF66', padding: '2vh 0' }}>9%</div>
                </div>
                <div style={{ width: '40vh', margin: '2vh' }}>
                    <div style={{ borderRadius: '2vh 2vh 0 0', backgroundColor: '#99CC66', padding: '2vh 0' }}>Liquidity Fees</div>
                    <div className='color2' style={{ borderRadius: '0 0 2vh 2vh', backgroundColor: '#FFFF66', padding: '2vh 0' }}>2%</div>
                </div>
                <div style={{ width: '40vh', margin: '2vh' }}>
                    <div style={{ borderRadius: '2vh 2vh 0 0', backgroundColor: '#99CC66', padding: '2vh 0' }}>Buy Back + Burn</div>
                    <div className='color2' style={{ borderRadius: '0 0 2vh 2vh', backgroundColor: '#FFFF66', padding: '2vh 0' }}>1%</div>
                </div>
                <div style={{ width: '40vh', margin: '2vh' }}>
                    <div style={{ borderRadius: '2vh 2vh 0 0', backgroundColor: '#99CC66', padding: '2vh 0' }}>Marketing</div>
                    <div className='color2' style={{ borderRadius: '0 0 2vh 2vh', backgroundColor: '#FFFF66', padding: '2vh 0' }}>2%</div>
                </div>
            </div>
            <div className='color2' style={{ marginTop: '6vh', marginBottom: '6vh' }}>TOKEN FEATURES</div>

            <div style={{ backgroundColor: '#f3f3a5', borderRadius: '1vh', padding: '2vh', textAlign: 'left' }}>
                <div style={{ display: 'flex', alignItems: 'center', marginTop: '1vh' }}>
                    <img style={{ width: '4vh', height: '4vh' }} src={ReusdIcon}></img>
                    <div className='color2' style={{ marginLeft: '2vh' }}>BUSD Reflections</div>
                </div>
                <div>Earn automatic Binance pegged BUSD rewards by holding your $MMT  8% of every buy and sell transaction is redistributed to all token holders directly in wallets. You will receive the rewards automatically in your wallet or claim your rewards manually in our dApp Dashboard anytime. This effectively means that the native reflection of Evergrow works in parallel with Stable Yield, while exponentially increasing your holdings.</div>
                <div style={{ display: 'flex', alignItems: 'center', marginTop: '1vh' }}>
                    <img style={{ width: '4vh', height: '4vh' }} src={RewardsIcon}></img>
                    <div className='color2' style={{ marginLeft: '2vh' }}>Rewards Pool</div>
                </div>
                <div>Each transaction will have 1% fee to the reward pool. When the pool reaches a certain baseline, the reward pool will be triggered as long as there is a purchase behavior. Thus to get all the bonuses from the reward pool at once, the reward baseline will keep increasing after each bonus.  (The buyer participation amount is at least 1,000,000,000)</div>
                <div style={{ display: 'flex', alignItems: 'center', marginTop: '1vh' }}>
                    <img style={{ width: '4vh', height: '4vh' }} src={BuybackIcon}></img>
                    <div className='color2' style={{ marginLeft: '2vh' }}>Buy-Back Wallet</div>
                </div>
                <div>1% tokens are collected from every transaction and converted to BNB and are stored in Our contract. This wallet is known as BuyBack Wallet, when enabled it purchases Money Machine from exchanges and removes the purchased tokens permanently from circulating supply while simultaneously making green candles. More than 1.7% of tokens have been already burnt through the Buy-Back Mechanism which will keep increasing in the future.</div>
                <div style={{ display: 'flex', alignItems: 'center', marginTop: '1vh' }}>
                    <img style={{ width: '4vh', height: '4vh' }} src={LiquidityIcon}></img>
                    <div className='color2' style={{ marginLeft: '2vh' }}>Liquidity Pool</div>
                </div>
                <div>Our Smart contract deposits 4% from every buy/sell transaction as $Evergrow and $BNB into a liquidity pool. Pancakeswap fixes the low liquidity problems of DEXs through the liquidity Pools. A liquidity pool always contains a pair of tokens, Instead of maintaining an order book like traditional exchanges, Every Buy/Sell trade is matched with Liquidity in the Liquidity Pool. A bigger liquidity pool will provide more stability to $MMT price.</div>
                <div style={{ display: 'flex', alignItems: 'center', marginTop: '1vh' }}>
                    <img style={{ width: '4vh', height: '4vh' }} src={SharkIcon}></img>
                    <div className='color2' style={{ marginLeft: '2vh' }}>Anti-Whale Mechanism</div>
                </div>
                <div>Our Anti-Whale function ensures that No sale amounts higher than 0.125% of the total supply are possible at once. Initially, it was at 0.25% but after we burned 50% Tokens of the Total Supply, we have reduced the limit per transaction by 50%. The Anti-whale wont necessarily stop Whales from selling but having a maximum limit works as an indicator when a Whale is trying to sell their tokens and avoids price manipulations.</div>

            </div>



            <div className='color2' style={{ marginTop: '6vh' }}>$MMT STRUCTURE</div>
            <div style={{ marginTop: '2vh', fontSize: '3vh' }}>total supply 1,000,000,000,000,000</div>

            <ReactECharts option={option}></ReactECharts>
            <div className='color2' style={{ marginTop: '6vh' }}>Roadmap</div>
            <div style={{ display: 'flex', flexWrap: 'wrap', justifyContent: 'center', margin: '4vh 0', textAlign: 'left' }}>
                <div style={{ backgroundColor: '#ff6666ba', width: '50vh', borderRadius: '1vh', padding: '2vh', margin: '2vh' }}>
                    <div style={{ marginBottom: '1vh', textAlign: 'center' }}>Q4 2021</div>
                    <div style={{ display: 'flex', alignItems: 'center', marginTop: '1vh' }}>
                        <img style={{ width: '4vh', height: '4vh' }} src={CheckedIcon}></img>
                        <div style={{ fontSize: '3vh', marginLeft: '1vh' }}>Launched Website and Community</div>
                    </div>
                    <div style={{ display: 'flex', alignItems: 'center', marginTop: '1vh' }}>
                        <img style={{ width: '4vh', height: '4vh' }} src={CheckedIcon}></img>
                        <div style={{ fontSize: '3vh', marginLeft: '1vh' }}>Whitepaper</div>
                    </div>
                    <div style={{ display: 'flex', alignItems: 'center', marginTop: '1vh' }}>
                        <img style={{ width: '4vh', height: '4vh' }} src={UncheckIcon}></img>
                        <div style={{ fontSize: '3vh', marginLeft: '1vh' }}>Presale and Launch (lock Initial liquidity for 1 year)</div>
                    </div>

                    <div style={{ display: 'flex', alignItems: 'center', marginTop: '1vh' }}>
                        <img style={{ width: '4vh', height: '4vh' }} src={UncheckIcon}></img>
                        <div style={{ fontSize: '3vh', marginLeft: '1vh' }}>Audit</div>
                    </div>
                    <div style={{ display: 'flex', alignItems: 'center', marginTop: '1vh' }}>
                        <img style={{ width: '4vh', height: '4vh' }} src={UncheckIcon}></img>
                        <div style={{ fontSize: '3vh', marginLeft: '1vh' }}>Listing on Coingecko and Coinmarketcap</div>
                    </div>
                </div>
                <div style={{ backgroundColor: '#ff6666ba', width: '50vh', borderRadius: '1vh', padding: '2vh', margin: '2vh' }}>
                    <div style={{ marginBottom: '1vh', textAlign: 'center' }}>Q1 2022</div>
                    
                    <div style={{ display: 'flex', alignItems: 'center', marginTop: '1vh' }}>
                        <img style={{ width: '4vh', height: '4vh' }} src={UncheckIcon}></img>
                        <div style={{ fontSize: '3vh', marginLeft: '1vh' }}>Aggressive marketing</div>
                    </div>
                    <div style={{ display: 'flex', alignItems: 'center', marginTop: '1vh' }}>
                        <img style={{ width: '4vh', height: '4vh' }} src={UncheckIcon}></img>
                        <div style={{ fontSize: '3vh', marginLeft: '1vh' }}>Make more Partnerships</div>
                    </div>
                    <div style={{ display: 'flex', alignItems: 'center', marginTop: '1vh' }}>
                        <img style={{ width: '4vh', height: '4vh' }} src={UncheckIcon}></img>
                        <div style={{ fontSize: '3vh', marginLeft: '1vh' }}>More usage scenarios of $MMT</div>
                    </div>
                    <div style={{ display: 'flex', alignItems: 'center', marginTop: '1vh' }}>
                        <img style={{ width: '4vh', height: '4vh' }} src={UncheckIcon}></img>
                        <div style={{ fontSize: '3vh', marginLeft: '1vh' }}>More feature of $MMT</div>
                    </div>
                    <div style={{ display: 'flex', alignItems: 'center', marginTop: '1vh' }}>
                        <img style={{ width: '4vh', height: '4vh' }} src={UncheckIcon}></img>
                        <div style={{ fontSize: '3vh', marginLeft: '1vh' }}>Money Machine NFT launch</div>
                    </div>
                </div>
                <div style={{ backgroundColor: '#ff6666ba', width: '50vh', borderRadius: '1vh', padding: '2vh', margin: '2vh' }}>
                    <div style={{ marginBottom: '1vh', textAlign: 'center' }}>Q2 2022</div>
                    <div style={{ display: 'flex', alignItems: 'center', marginTop: '1vh' }}>
                        <img style={{ width: '4vh', height: '4vh' }} src={UncheckIcon}></img>
                        <div style={{ fontSize: '3vh', marginLeft: '1vh' }}>Launch a game based on Moneny Machine NFT</div>
                    </div>
                    <div style={{ display: 'flex', alignItems: 'center', marginTop: '1vh' }}>
                        <img style={{ width: '4vh', height: '4vh' }} src={UncheckIcon}></img>
                        <div style={{ fontSize: '3vh', marginLeft: '1vh' }}>More usage scenarios of Moneny Machine NFT</div>
                    </div>
                    <div style={{ display: 'flex', alignItems: 'center', marginTop: '1vh' }}>
                        <img style={{ width: '4vh', height: '4vh' }} src={UncheckIcon}></img>
                        <div style={{ fontSize: '3vh', marginLeft: '1vh' }}>TBD</div>
                    </div>

                </div>


            </div>
            <div className='color2' style={{ marginTop: '6vh' }}>JOIN OUR COMMUNITY</div>
            <div style={{ display: 'flex', justifyContent: 'center', margin: '6vh 0' }}>
                <a href='https://mobile.twitter.com/money_machine__' target='_blank'>
                    <img style={{ width: '8vh', height: '8vh' }} src={TW}></img>
                    <div className='color3'>Twitter</div>
                </a>
                <a href='https://t.me/MoneyMachine_Official' target='_blank' style={{ marginLeft: '4vh' }}>
                    <img style={{ width: '8vh', height: '8vh' }} src={TG}></img>
                    <div className='color3'>Telegram</div>
                </a>
            </div>




        </div>
    )
}

export default Index