import { busdAddress, dividendsAddress, mmtAddress } from '../constant/address'
import mmtAbi from '../constant/abi/mmt.json'
import dividendsAbi from '../constant/abi/dividends.json'
import Web3 from 'web3'

export const getMMTBalance = async (owner) => {
    const web3 = new Web3(Web3.givenProvider || "http://localhost:8545")
    const contract = new web3.eth.Contract(mmtAbi, mmtAddress)
    return contract.methods.balanceOf(owner).call()
}
export const getBUSDBalance = async (owner) => {
    const web3 = new Web3(Web3.givenProvider || "http://localhost:8545")
    const contract = new web3.eth.Contract(mmtAbi, busdAddress)
    return contract.methods.balanceOf(owner).call()
}
export const getUnpaidEarnings=async(owner)=>{
    const web3 = new Web3(Web3.givenProvider || "http://localhost:8545")
    const contract = new web3.eth.Contract(dividendsAbi.abi, dividendsAddress)
    return contract.methods.getUnpaidEarnings(owner).call()
}
export const getTotalEarn=async(owner)=>{
    const web3 = new Web3(Web3.givenProvider || "http://localhost:8545")
    const contract = new web3.eth.Contract(dividendsAbi.abi, dividendsAddress)
    return contract.methods.shares(owner).call()
}
