export const getDividends = async () => {
    const web3 = new Web3(Web3.givenProvider || "http://localhost:8545")
    const contract = new web3.eth.Contract(dividendsAbi.abi, dividendsAddress)
    const res = await contract.methods.getList().call()
    console.log(res,'list')
    let data={}
    data.list=[]
    if(res){
        let obj={
            address:res[0],
            reward:res[1],
            block:res[2]
        }
        let obj2={
            address:res[3],
            reward:res[4],
            block:res[5]
        }
        let obj3={
            address:res[6],
            reward:res[7],
            block:res[8]
        }
        data.list=[obj,obj2,obj3]
    }
    const baseline= await contract.methods.rewardThreshold().call()
    console.log(baseline,'baseline')
    data.baseline=baseline
    const totalDistributed=await contract.methods.totalDistributed().call()
    data.totalDistributed=totalDistributed
    const totalPoolDistributed=await contract.methods.totalPoolDistributed().call()
    data.totalPoolDistributed=totalPoolDistributed
    return data
    
}
